import goodBox from "../../components/common/goodBox.vue"
export default {
	components: {
		goodBox
	},
	data() {
		return {
			subList: null,
			active: null,
			currentNav: null,
			goodList: []
		};
	},
	methods: {
		getCategoryNav(id) {
			this.$api.getCategoryNav(id).then(res => {
				// console.log(res);
				this.subList = res.data.navData
				this.active = res.data.currentNav.id
				this.getCategoryGoods(id)
			})
		},
		changeNav(id) {
			this.active = id
			this.getCategoryNav(id)
		},
		getCategoryGoods(id) {
			this.$api.getCategoryGoods(id).then(res => {
				this.currentNav = res.data.currentNav
				this.goodList = res.data.data
			})
		}
	},
	onLoad(options) {
		this.getCategoryNav(options.id)
	}
}
